@media print {
  $size: 2.6mm;
  $margin: 10mm;

  @page {
    padding: 0;
    margin: 0;
  }

  table {
    margin: $margin auto 0 auto !important;
  }

  body {
    width: 100% !important;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto !important;
  }

  html, body {overflow: hidden;}
  td {
    width: $size;
    height: $size;
    //border-width: 0.5mm;
    border-width: 1mm;
  }
  body {
    //margin: $margin 0 0 0 !important;
    background: #fff !important;
    padding: 0 !important;
  }

  .row-1::after {
    content: '----';
    position: absolute;
    font-size: $size;
    color: #000;
  }

  .content, .set {display: none  !important;}
}





html,
body {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}

table {
  box-sizing: border-box;
  border-collapse: collapse;
  background: #fff;
  margin: auto;
  border-spacing: 0;
}

td {
  width: $size;
  height: $size;
  border-width: 1px;
  overflow: hidden;
}


