//.row-65 .week:nth-child(-n+6)::before {border-color: green}

.week {
  border: $border solid #000;
  position: relative;
  overflow: hidden;


  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 0;
    height: 0;
    margin: auto;
    border: $size solid #ffffff;
  }
}

