$plus: 10;

@media (min-width: 594px + $plus) {
  body {background: green;}
  td {$size: 8px;  width: $size;  height: $size;}
  //table { box-shadow: 0 0 5px 0 #000}
}

@media (max-width: 593px + $plus) {
  body {background: blue;}
  td {$size: 7px;  width: $size;  height: $size;}
}

@media (max-width: 539px + $plus) {
  body {background: yellow;}
  td {$size: 6px;  width: $size;  height: $size;}
}

@media (max-width: 485px + $plus) {
  body {background: #ff4391;}
  td {$size: 5px;  width: $size;  height: $size;}
}

@media (max-width: 431px + $plus) {
  body {background: #96ffba;}
  td {$size: 4px;  width: $size;  height: $size;}
}

@media (max-width: 377px + $plus) {
  body {background: red;}
  td {$size: 3px;  width: $size;  height: $size;}
}

@media (max-width: 323px + $plus) {
  body {background: olive;}
  td {$size: 2px;  width: $size;  height: $size;}
}

@media (max-width: 269px + $plus) {
  body {background: #2fff00;}
  td {$size: 1px;  width: $size;  height: $size;}
}
