.set {
  &-input:checked ~ &-label {
    background:
      url("data:image/svg+xml;base64,")
      no-repeat
      1px -1px;
    background-size: $btn-size;
  }
    &-input:checked ~ &-print {
    background:
      url("data:image/svg+xml;base64,")
      no-repeat
      1px -1px;
    background-size: $btn-size;
  }

  &-input:checked ~ .content {
    left: 0;
    //right: 0;
    //transition: all .5s;
  }
  &-btn {
    position: fixed;
    width: $btn-size;
    height: $btn-size;
    top: 10px;
    z-index: 9999;
    opacity: .7;
    background-size: $btn-size;
    &:hover {
      opacity: 1;
    }
  }
  &-print {
    right: 10px;
    background:
  url("data:image/svg+xml;base64,")
    no-repeat
    center;
    background-size: $btn-size;
  }
  &-label {
    left: 10px;
    border-radius: 50%;
    background:
      url("data:image/svg+xml;base64,")
      no-repeat
      center;
    background-size: $btn-size;
  }
  &-input {
    display: none;
  }
}

















.content {
  $width: 640px;
  $background: #111;
  $font-color: #fff;
  $border-color: #111;
  color: $font-color;
  position: fixed;
  top: 0;
  left: -9999px;
  right: 0;
  bottom: 0;
  margin: auto;
  max-width: $width;
  background: $background linear-gradient(lighten($background, 20%), #000);
  transition: left .4s;
  box-shadow: 0 0 20px 0 #000;
  box-sizing: border-box;
  &-box {
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    margin: auto;
  }
  &-text, &-title {
    margin: 0;
  }
  &-label {
    display: block;
    border: 1px solid #888;
    text-align: center;
    font: #{$btn-size/2}/#{$btn-size} Tahoma;
    color: #fff;
    text-shadow: 0 0 2px #000;
    text-transform: capitalize;
    &:nth-child(-n +3) {
      margin: 0 0 10px 0;
    }
    &-color {
      height: $btn-size;
      &-favorite {
        background: #00ffff;
      }
      &-fill {
        background: #000000;
      }
    }
      &-submit {
        display: none;
      }

    &-btn {
      height: $btn-size;
      border: none;
      outline: none;
      background:
        #0f0
        url("data:image/svg+xml;base64,")
        no-repeat
        center -1px;
      background-size: $btn-size;
    }
  }
}

