/* main.scss */
.sprite {
  background: url("data:image/svg+xml;base64,[data]");
}

@media print {
  @page {
    padding: 0;
    margin: 0;
  }
  table {
    margin: 10mm auto 0 auto !important;
  }
  body {
    width: 100% !important;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto !important;
  }
  html, body {
    overflow: hidden;
  }
  td {
    width: 2.6mm;
    height: 2.6mm;
    border-width: 1mm;
  }
  body {
    background: #fff !important;
    padding: 0 !important;
  }
  .row-1::after {
    content: '----';
    position: absolute;
    font-size: 2.6mm;
    color: #000;
  }
  .content, .set {
    display: none  !important;
  }
}

html,
body {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}

table {
  box-sizing: border-box;
  border-collapse: collapse;
  background: #fff;
  margin: auto;
  border-spacing: 0;
}

td {
  width: 5px;
  height: 5px;
  border-width: 1px;
  overflow: hidden;
}

.col-11,
.col-21,
.col-31,
.col-41,
.col-51,
.col-6,
.col-16,
.col-26,
.col-36,
.col-46 {
  border-right: 1px dotted #000;
}

.col-14,
.col-27,
.col-40 {
  border-right: 1px solid #000;
}

.row-10 .both,
.row-20 .both,
.row-30 .both,
.row-40 .both,
.row-50 .both,
.row-60 .both,
.row-70 .both,
.row-80 .both,
.row-90 .both {
  border-bottom: 1px solid #000;
}

.row-5 .both,
.row-15 .both,
.row-25 .both,
.row-35 .both,
.row-45 .both,
.row-55 .both,
.row-65 .both,
.row-75 .both,
.row-85 .both,
.row-95 .both {
  border-bottom: 1px dotted #000;
}

.week {
  border: 1px solid #000;
  position: relative;
  overflow: hidden;
}

.week::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 0;
  height: 0;
  margin: auto;
  border: 5px solid #ffffff;
}

@media (min-width: 604px) {
  body {
    background: green;
  }
  td {
    width: 8px;
    height: 8px;
  }
}

@media (max-width: 603px) {
  body {
    background: blue;
  }
  td {
    width: 7px;
    height: 7px;
  }
}

@media (max-width: 549px) {
  body {
    background: yellow;
  }
  td {
    width: 6px;
    height: 6px;
  }
}

@media (max-width: 495px) {
  body {
    background: #ff4391;
  }
  td {
    width: 5px;
    height: 5px;
  }
}

@media (max-width: 441px) {
  body {
    background: #96ffba;
  }
  td {
    width: 4px;
    height: 4px;
  }
}

@media (max-width: 387px) {
  body {
    background: red;
  }
  td {
    width: 3px;
    height: 3px;
  }
}

@media (max-width: 333px) {
  body {
    background: olive;
  }
  td {
    width: 2px;
    height: 2px;
  }
}

@media (max-width: 279px) {
  body {
    background: #2fff00;
  }
  td {
    width: 1px;
    height: 1px;
  }
}

.set-input:checked ~ .set-label {
  background: url("data:image/svg+xml;base64,") no-repeat 1px -1px;
  background-size: 40px;
}

.set-input:checked ~ .set-print {
  background: url("data:image/svg+xml;base64,") no-repeat 1px -1px;
  background-size: 40px;
}

.set-input:checked ~ .content {
  left: 0;
}

.set-btn {
  position: fixed;
  width: 40px;
  height: 40px;
  top: 10px;
  z-index: 9999;
  opacity: .7;
  background-size: 40px;
}

.set-btn:hover {
  opacity: 1;
}

.set-print {
  right: 10px;
  background: url("data:image/svg+xml;base64,") no-repeat center;
  background-size: 40px;
}

.set-label {
  left: 10px;
  border-radius: 50%;
  background: url("data:image/svg+xml;base64,") no-repeat center;
  background-size: 40px;
}

.set-input {
  display: none;
}

.content {
  color: #fff;
  position: fixed;
  top: 0;
  left: -9999px;
  right: 0;
  bottom: 0;
  margin: auto;
  max-width: 640px;
  background: #111 linear-gradient(#444444, #000);
  transition: left .4s;
  box-shadow: 0 0 20px 0 #000;
  box-sizing: border-box;
}

.content-box {
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  margin: auto;
}

.content-text, .content-title {
  margin: 0;
}

.content-label {
  display: block;
  border: 1px solid #888;
  text-align: center;
  font: 20px/40px Tahoma;
  color: #fff;
  text-shadow: 0 0 2px #000;
  text-transform: capitalize;
}

.content-label:nth-child(-n +3) {
  margin: 0 0 10px 0;
}

.content-label-color {
  height: 40px;
}

.content-label-color-favorite {
  background: #00ffff;
}

.content-label-color-fill {
  background: #000000;
}

.content-label-submit {
  display: none;
}

.content-label-btn {
  height: 40px;
  border: none;
  outline: none;
  background: #0f0 url("data:image/svg+xml;base64,") no-repeat center -1px;
  background-size: 40px;
}

.test {
  content: '';
}

